	module.exports = {"data":{"markdownRemark":{"html":"<p>You notify programs of vulnerabilities by submitting reports to the program's inbox. Not all great vulnerability reports look the same, but many share these common features:</p>\n<ul>\n<li>Detailed descriptions of your discovery with clear, concise, reproducible steps or a working proof-of-concept (POC). If you doesn't explain the vulnerability in detail, there may be significant delays in the disclosure process, which is undesirable for everyone.</li>\n<li>Screenshots and/or videos can assist your security teams to quickly reproduce the issue if your program accepts them. Make sure the program states their policy regarding screenshots and videos on their security page and scope as not all programs accept them.</li>\n</ul>\n<h3>Examples</h3>\n<p>Here are some examples of publicly disclosed examples of good reports:</p>\n<ul>\n<li><a href=\"https://hackerone.com/reports/32825\">Twitter disclosed on HackerOne: URGENT - Subdomain Takeover</a></li>\n<li><a href=\"https://hackerone.com/reports/73567\">Shopify disclosed on HackerOne: Attention! Remote Code Execution</a></li>\n<li><a href=\"https://hackerone.com/reports/38682\">Square disclosed on HackerOne: Delayed, fraudulent transactions</a></li>\n</ul>\n<p>Some great resources for vulnerability report best practices are:</p>\n<ul>\n<li><a href=\"https://blogs.dropbox.com/tech/2015/08/dropbox-bug-bounty-program-best-practices-2/\">Dropbox Bug Bounty Program: Best Practices</a></li>\n<li><a href=\"https://sites.google.com/site/bughunteruniversity/\">Google Bug Hunter University</a></li>\n<li><a href=\"https://www.facebook.com/notes/facebook-bug-bounty/a-bounty-hunters-guide-to-facebook/946955115318715\">A Bounty Hunter’s Guide to Facebook</a></li>\n<li><a href=\"https://medium.com/@tolo7010/writing-a-good-and-detailed-vulnerability-report-bdb86cedcff\">Writing a good and detailed vulnerability report</a></li>\n</ul>","frontmatter":{"title":"Quality Reports","id":"hackers/quality-reports","path":"/hackers/quality-reports.html","description":null}}},"pathContext":{}}

